@import "./defs.css";

.input-container {
  height: 90%;
  align-items: center;
  align-content: center;
  margin-left: 5px;
  margin-right: 5px;
  padding: 0;
  overflow: hidden;
}

.input-container > input {
  width: auto;
  min-height: 80%;
  height: auto;
  padding: 2% 15px;
  font-size: 85%;
  border: 0;
  background-color: rgba(0, 0, 0, 0.25);
  border-radius: 35px;
  -webkit-border-radius: 35px;
  -moz-border-radius: 35px;
  -ms-border-radius: 35px;
  -o-border-radius: 35px;
}

.input-container > input:focus {
  width: 80%;
  outline: none;
  -webkit-box-shadow: inset 0px 2px 11px -5px rgba(0, 0, 0, 0.52);
  -moz-box-shadow: inset 0px 2px 11px -5px rgba(0, 0, 0, 0.52);
  box-shadow: inset 0px 2px 11px -5px rgba(0, 0, 0, 0.52);
}

.have-icon {
  padding-right: 50px;
}
.show {
  animation: appear 1s ease-out;
  -webkit-animation: appear 1s ease-out;
  width: 80%;
}

.hide {
  animation: hidde 1s ease-out;
  -webkit-animation: hidde 1s ease-out;
  width: 0%;
}

@keyframes appear {
  from {
    visibility: hidden;
    opacity: 0;
    width: 0%;
  }

  to {
    opacity: 1;
    width: 80%;
    visibility: visible;
  }
}

@keyframes hidde {
  from {
    opacity: 1;
    width: 80%;
  }

  to {
    opacity: 0;
    width: 0%;
    visibility: hidden;
  }
}

@keyframes makeSmall {
  from {
    font-size: small;
  }

  to {
    font-size: smaller;
  }
}
