@import "./defs.css";

.nav {
  position: fixed;
  display: block;
  z-index: 1;
  top: 0px;
  left: 0;
  padding: 0px 0px;
  margin: 0;
  min-width: 100%;
  height: 50px;
  min-height: 30px;
  align-items: center;
  font-size: calc(var(--font-size) * 0.65);
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  align-content: center;
  overflow: visible;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}

.nav-container {
  display: flex;
  float: left;
  height: 100%;
  min-height: 90%;
  padding: 0;
  align-items: center;
  justify-content: center;
  background-color: var(--primary-color);
}

.nav-item {
  float: right;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80%;
  min-height: 40%;
  max-height: 100%;
  padding: 0px 20px;
  margin: 0px 4px;
  text-decoration: none;
  color: var(--foreground-color);
  border: 0px solid var(--primary-color);
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.nav-item:hover {
  color: var(--hovered-color);
  cursor: pointer;
  /*border-bottom: 3px solid var(--primary-color);*/
}

.nav-item:active {
  color: var(--active-color);
}

.nav-active {
  text-rendering: geometricPrecision;
  border-radius: 24px;
  -webkit-border-radius: 24px;
  -moz-border-radius: 24px;
  -ms-border-radius: 24px;
  -o-border-radius: 24px;
  /* background:var(--secondary-color);
    background: linear-gradient(45deg, var(--secondary-color) 3%, var(--primary-color));*/
  font: bold;
}

.nav-logo {
  height: 90%;
  margin-left: 10%;
}

@keyframes itemHover {
  from {
    border-bottom: inherit;
  }

  to {
    border-bottom: 3px solid var(--primary-color);
  }
}

@keyframes setActive {
  0% {
    border-bottom: 3px solid var(--primary-color);
  }

  100% {
    -webkit-box-shadow: inset 0px 6px 8px -8px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: inset 0px 6px 8px -8px rgba(0, 0, 0, 0.75);
    box-shadow: inset 0px 6px 8px -8px rgba(0, 0, 0, 0.75);
  }
}
