@import './defs.css';

.search-container {
    position:relative;
    padding:0 2px;
    min-height: 90%;
    display:flex;
    align-items: center;
}

.search-container>img {
    display: block;
    float: right;
    max-height: 90%;
    height:25px;
    z-index: 2;
    margin-left: -50px;

}
.search-container>img:hover{
   background:border-box;
}

.search-container>div{
    display: inline;
}

.float-right{
    float: right;
}